body {
  --white: #F5F5F5;
  --pink: #EF959D;
  --purple: #5D2E8C;
  --turquoise: #62BEC1;
  --black: #222222;
  --dark-gray: #343434;
  --light-gray: #828282;
  background-color: var(--black) !important;
  color: var(--white);
  font-family: 'Montserrat', sans-serif;
}

/* Landing Page */
.select {
  text-align: center;
  padding: 15px 30px;
  font-weight: 400;
  font-size: 30px;
  border-radius: 40px;
  color: var(--white);
}

.select:hover {
  color: inherit;
  text-decoration: none;
}

.work-btn {
  background-color: var(--turquoise);
}

.sleep-btn {
  background-color: var(--purple);
}

.birth-btn {
  background-color: var(--pink);
}

/* Work Page */
circle {
  transition: stroke-dashoffset 0.35s;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.title {
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  animation: fadeIn 1.5s ease-in;
}

@keyframes fadeIn {
0% {opacity:0;}
100% {opacity:1;}
}

.line {
  display:block;
  margin: 0 auto;
  border-bottom: 2px solid var(--white);
  width: 0vw;
  text-align:center;
  animation: line 1s linear forwards;
}

@keyframes line {
  from {
    width:0vw;
  }
  to {
    width:65vw;
  }
}

.task-group {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default checkbox */
.task-group input[type=checkbox] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  cursor: pointer;
  background-color: transparent;
  border: 2px solid var(--white);
}

/* On mouse-over, add a grey background color */
.checkmark:hover {
  background-color: var(--dark-gray);
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.task-group input:checked ~ .checkmark:after {
  display: block;
}

.task-group .add-btn {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: transparent;
  color: var(--light-gray);
  border: 2px solid var(--light-gray);
}

.task-entry {
  border: 0px;
  background-color: transparent;
  color: var(--light-gray);
}

.task-entry:active, .task-entry:focus, .task-entry::selection {
  color: var(--white);
}

/* Style the checkmark/indicator */
.task-group .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
